.container {
    background-color: rgb(221, 219, 219);
    margin-bottom: 50px;
    border-radius: 10px;
    font-size: 21px;
}
@media (max-width: 600px) {
    .education,.skills {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

#about .row {
    width: 100%;
}

#about .contain {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 90%;
}

.about-me {
    text-align: center;
    width: 50%;
    margin: auto;
}

.about-me h1 {
    text-decoration: underline;
}

.stars span {
    float: right;
    margin-right: 20%;
}
.stars p {
    font-weight: 600;
    margin-bottom: 5px;
}
.tab {
    margin-left: 30px;
}

/* education part to line up the text */
.education {
    text-align: left;
}
.education h1,
.skills h1 {
    text-align: center;
}
.education h3 {
    font-weight: 600;
}

.date {
    font-weight: 100;
    font-style: italic;
    margin-left: 10px;
}

.skills {
    text-align: left;
}

.skills ul li {
    list-style: none;
}

.stars {
    align-items: center;
}

.projects-container {
  background-color: #dddbdb;
  margin: auto;
  padding: 5px 0;
  border-radius: 10px;
  font-size: 20px;
}

.projects-container p {
  margin: 0;
  padding: 0;
  text-align: left;
}

@media (min-height: 768px) {
  .projects-container .projects {
    display: flex;
  }
}

@media (max-width: 768px) {
  .projects-container .projects {
    flex: 0 0 83.333333%;
    max-width: 83.333333%;
  }
}

.projects-container h1,
.projects-container h2 {
  text-decoration: underline;
}

@media (min-width: 768px) {
  .projects-container .row {
    display: flex;
  }
}

.projects-container .contain {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media (min-width: 768px) {
  .projects-container .contain {
    margin: 0 auto;
  }
}

.projects-container .contain .row {
  width: 100%;
}

@media (min-width: 768px) {
  .projects-container .col {
    margin: 0 5px;
    margin-bottom: 35px;
  }
}

.projects-container .col .image-container {
  position: relative;
}

.projects-container .col .image-container .image {
  width: 100%;
  height: 100%;
}
